<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Daerah</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                            @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Daerah
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="daerahpenjualan" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key="item.KodeDaerah">
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["KodeDaerah"] }} </td>
                                <td class="title">{{ item["Nama Daerah"]}}</td>
                                <td class="title">{{ item["Nama Sales"] }}</td>
                                <td class="title">
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click="deleteData(item.KodeDaerah)">
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialog" persistent max-width="950" @keydown.esc="dialog = false">
            <v-card>
                <v-card-title>
                    <span class="headline">Daerah</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="2">
                                <h3 class="mt-3">Kode Daerah</h3>
                            </v-col>
                            <v-col class="mr-12" cols="3">
                                <v-text-field dense single-line outlined label="Kode Daerah*" v-model="form.kodedaerah" required>
                                    </v-text-field>
                            </v-col>
                            <v-col class="ml-6" cols="2">
                                <h3 class="mt-3">Nama Daerah</h3>
                            </v-col>
                            <v-col class="mr-12" cols="3">
                                <v-text-field dense single-line outlined label="Nama Daerah*" v-model="form.namadaerah" required></v-text-field>
                            </v-col>
                            <v-col class="mr-12" cols="12">
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-3">Nama Sales</h3>
                            </v-col>
                            <v-col class="ml-1" cols="3">
                                <v-text-field dense single-line outlined label="Nama Sales*" v-model="form.namasales" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                        <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
export default {
    data() {
        return {
            statusUpdate: true,
            dialog: false,
            keyword: '',
            headers: [{
                    text: 'No',
                    value: 'no',
                    class: 'title'
                },
                {
                    text: 'Kode Daerah',
                    value: 'KodeDaerah',
                    class: 'title'
                },
                {
                    text: 'Nama Daerah',
                    value: 'NamaDaerah',
                    class: 'title'
                },
                {
                    text: 'Nama Sales',
                    value: 'NamaSales',
                    class: 'title'
                },
                {
                    text: 'Aksi',
                    value: null,
                    class: 'title'
                },
            ],
            daerahpenjualan: [],
            snackbar: false,
            color: null,
            text: '',
            load: false,
            form: {
                kodedaerah: '',
                namadaerah: '',
                namasales: '',
            },
            daerah: new FormData,
            typeInput: 'new',
            errors: '',
            updatedId: '',
        }
    },
    methods: {
        getData() {
            var uri = this.$apiUrl + 'daerah'
            this.$http.get(uri).then(response => {
                this.daerahpenjualan = response.data
                console.log(this.daerahpenjualan)
            })
        },
        Activity(key) {
            // untuk memposting aktivitas user yang login
            var activity = new FormData
            activity.append('activity', key)
            var uri = this.$apiUrl + 'activity'
            this.$http.post(uri, activity).then(response => {}).catch(error => {
                this.errors = error
            })
        },
        sendData() {
        this.daerah.append('kodedaerah', this.form.kodedaerah);
        this.daerah.append('namadaerah', this.form.namadaerah);
        this.daerah.append('namasales', this.form.namasales);
        var uri = this.$apiUrl + 'daerah'
        this.load = true
        this.$http.post(uri, this.daerah).then(response => {
            this.snackbar = true; //mengaktifkan snackbar
            this.color = 'green'; //memberi warna snackbar
            this.text = response.data; //memasukkan pesan ke snackbar
            this.load = false;
            this.dialog = false
            this.getData(); //mengambil data user
            this.resetForm();
            this.Activity('Melakukan Create Daerah');
        }).catch(error => {
            this.errors = error
            this.snackbar = true;
            this.text = 'Try Again';
            this.color = 'red';
            this.load = false;
        })
      },
        updateData() {
            this.daerah.append('kodedaerah', this.form.kodedaerah);
            this.daerah.append('namadaerah', this.form.namadaerah);
            this.daerah.append('namasales', this.form.namasales);
            var uri = this.$apiUrl + 'daerah/' + this.updatedId;
            this.load = true
            this.$http.post(uri, this.daerah).then(response => {
                this.snackbar = true; //mengaktifkan snackbar
                this.color = 'green'; //memberi warna snackbar
                this.text = response.data; //memasukkan pesan ke snackba
                this.load = false;
                this.dialog = false
                this.statusUpdate = true
                this.getData(); //mengambil data user
                this.resetForm();
                this.Activity('Melakukan Update Daerah');
                this.typeInput = 'new';
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
                this.load = false;
                this.typeInput = 'new';
            })
        },
        editHandler(item) {
            this.typeInput = 'edit';
            this.dialog = true;
            this.statusUpdate = false;
            this.form.kodedaerah = item["KodeDaerah"];
            this.form.namadaerah = item["Nama Daerah"];
            this.form.namasales = item["Nama Sales"];
            this.updatedId = item["KodeDaerah"]
        },
        deleteData(deleteId) { //mengahapus data
            var uri = this.$apiUrl +
                'daerah/' + deleteId; //data dihapus berdasarkan id
            this.$http.delete(uri).then(response => {
                this.snackbar = true;
                this.text = response.data;
                this.color = 'green'
                this.deleteDialog = false;
                this.getData();
                this.Activity('Melakukan Delete Daerah');
            }).catch(error => {
                this.errors = error
                this.snackbar = true;
                this.text = 'Try Again';
                this.color = 'red';
            })
        },
        setForm() {
            if (this.typeInput === 'new') {
                this.sendData()
            } else {
                this.updateData()
            }
        },
        close() {
            this.dialog = false
            this.resetForm()
        },
        resetForm() {
            this.form = {
                kodedaerah: '',
                namadaerah: '',
                namasales: ''
            }
            this.statusUpdate = true
        }
    },
    mounted() {
        this.getData();
    },
    
}
</script>